import { Injectable } from '@angular/core';

export interface CsvHeaderMapping {
  label: string; // Header name in CSV
  key: string; // Key from JSON
}

@Injectable({
  providedIn: 'root',
})
export class RmNgExportToCsvService {
  exportAsCSV<T extends Record<string, any>>(
    data: T[],
    filename: string = 'export.csv',
    headers?: CsvHeaderMapping[],
  ): void {
    if (!data || !data.length) return;

    let keys: string[] = [];
    let headerRow: string[] = [];

    if (headers && headers.length) {
      // Use custom headers
      keys = headers.map((h) => h.key);
      headerRow = headers.map((h) => h.label);
    } else {
      // Use keys from the first object
      keys = Object.keys(data[0]);
      headerRow = keys;
    }

    const csvRows = [];
    csvRows.push(headerRow.join(','));

    for (const row of data) {
      const values = keys.map((key) => {
        const cell = row[key] ?? '';
        return `"${cell.toString().replace(/"/g, '""')}"`;
      });
      csvRows.push(values.join(','));
    }

    const csvContent = csvRows.join('\r\n');
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    const url = URL.createObjectURL(blob);

    link.setAttribute('href', url);
    link.setAttribute('download', filename);
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
}
